.transition {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  margin: 0; }

.transition__strip {
  height: 100%;
  background-color: #0e0e0e; }

.transition__strip-appear, .transition__strip-enter {
  height: 0; }

.transition__strip-appear-active, .transition__strip-enter-active {
  height: 100%;
  transition: all 1s ease; }

.transition__strip-exit-active {
  height: 0;
  transition: all 1s ease; }
